<template>
  <div class="service" id="services">
    <div class="container">
      <div class="service__content" ref="content">
        <div class="service__content_item">
          <div
            class="service__content_item-services"
            v-for="(item, index) in ServiceContent"
            :key="index"
            :class="{ active: item.isActive }"
            @click="serviseCard(index)"
          >
            <div class="line"></div>
            <span
              class="service__content_item-services-num"
              :class="{ active: item.isActive }"
              >{{ item.num }}</span
            >
            <h3 class="service__content_item-services-title">
              {{ item.title[activeLanguage] }}
            </h3>
            <p
              class="service__content_item-services-descr"
              v-html="item.descr[activeLanguage]"
            ></p>
          </div>
        </div>
        <div class="service__content_item-txt">
          <div
            class="service__content_item-txt-info"
            v-for="(item, index) in ServiceContent"
            :key="index"
            :class="{ active: item.isActive }"
          >
            <h3
              class="service__content_item-txt-info-title"
              v-html="item.infoTitle[activeLanguage]"
            ></h3>
            <p
              class="service__content_item-txt-info-descr"
              v-html="item.infoDescr[activeLanguage]"
            ></p>
          </div>
        </div>
      </div>
      <swiper
        class="service__content-mob"
        modules
        :pagination="{
          el: '.swiper-pagination',
          type: 'bullets',
          clickable: true,
        }"
        :breakpoints="{
          980: {
            slidesPerView: 4,
          },
          769: {
            slidesPerView: 3,
          },
          768: {
            slidesPerView: 1,
          },
        }"
      >
        <swiper-slide
          class="service__content-swiper"
          v-for="(item, index) in ServiceContent"
          :key="index"
        >
          <div class="service__content-swiper_item">
            <span
              class="service__content-swiper_item-num"
              :class="{ active: item.isActive }"
              >{{ item.num }}</span
            >
            <h3 class="service__content-swiper_item-title">
              {{ item.title[activeLanguage] }}
            </h3>
            <p
              class="service__content-swiper_item-descr"
              v-html="item.descr[activeLanguage]"
            ></p>
            <div class="line"></div>
            <p
              class="service__content-swiper_item-info-descr"
              v-html="item.infoDescr[activeLanguage]"
            ></p>
          </div>
          <h6 class="service__content-swiper_item-info-slide">
            {{ item.slide }}
          </h6>
        </swiper-slide>
        <div class="swiper-pagination"></div>
      </swiper>
      <img class="swipe-icon" src="../assets/images/icons/swipe.png" alt="" />
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import SwiperCore, { Pagination, Navigation } from "swiper";
import "swiper/swiper-bundle.css";
SwiperCore.use([Pagination, Navigation]);
export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      ServiceContent: [
        {
          num: "1.",
          title: {
            ru: "Market Research ",
            uz: `Market Research `,
            en: "Market Research ",
          },
          descr: {
            ru: "(иследование рынка)",
            uz: `(иследование рынка)`,
            en: "",
          },
          infoTitle: {
            ru: "MARKET RESEARCH <span>(иследование рынка)</span>",
            uz: ``,
            en: "",
          },
          infoDescr: {
            ru: "<b>Мы собираем и анализируем данные о ситуации на рынке товаров.</b><br><br> <span>В процессе исследования собираем различную информацию, например, показатели по дистрибуции, цены на аналогичную продукцию у других производителей и продавцов, данные о ваших конкурентах, информацию про вашу целевую аудиторию и т. п.</span>",
            uz: ``,
            en: "",
          },
          slide: "RETAIL AUDIT →",
          isActive: true,
        },
        {
          num: "2.",
          title: { ru: "RETAIL AUDIT", uz: `RETAIL AUDIT`, en: "RETAIL AUDIT" },
          descr: {
            ru: "( розничный аудит )",
            uz: ``,
            en: "",
          },
          infoTitle: {
            ru: "RETAIL AUDIT <span>(розничный аудит)</span>",
            uz: ``,
            en: "",
          },
          infoDescr: {
            ru: "<b>Наше агенство успешно проводит розничный аудит.</b> <br><br> <span>Мы можем замерять как стандартные параметры по товарам, это замеры по ассортименту, представленности, долям по выкладке, объемам продаж, наличию рекламных материалов и т. п., так и по выставленным KPI для торговых сил, команд и дистрибуторов.</span>",
            uz: ``,
            en: "",
          },
          slide: "CENSUS →",
          isActive: false,
        },
        {
          num: "3.",
          title: { ru: "CENSUS", uz: `CENSUS`, en: "CENSUS" },
          descr: {
            ru: "(перепись)",
            uz: `(перепись)`,
            en: "(перепись)",
          },
          infoTitle: {
            ru: "CENSUS <span>(перепись)</span>",
            uz: ``,
            en: "",
          },
          infoDescr: {
            ru: "<b>Мы проводим полную или частичную перепись торговых точек для формирования потенциальной клиентской базы для развития вашей дистрибуции, продаж и маркетинговых мероприятий.</b> <br><br> <span>Основные параметры, которые вы получите по каждой точке: юридическое название, фактический адрес, GPS-координаты, фото фасада, тип и категория точки, категории товаров, которые продаются в точке, которые вам необходимы, и т. п.</span>",
            uz: ``,
            en: "",
          },
          isActive: false,
        },
      ],
    };
  },
  methods: {
    serviseCard(index) {
      let toggle = false;
      this.ServiceContent.forEach((item, idx) => {
        if (index == idx && item.isActive) {
          toggle = true;
        }
        if (item.isActive) {
          item.isActive = false;
        }
      });
      if (!toggle) {
        this.ServiceContent[index].isActive = true;
      }
    },
  },
  computed: {
    activeLanguage() {
      return this.$store.getters.getLanguage;
    },
  },
  watch: {
    services() {
      this.ServiceContent = this.ServiceContent;
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";

.service {
  width: 100%;
  // max-width: 1920px;
  margin: 0 auto;
  background: linear-gradient(172.9deg, #8d167f 4.65%, #251c30 92.76%);
