  overflow: hidden;
  @media (max-width: 768px) {
    @include adaptivPaddingmd(0, 0, 70, 70, 0, 0, 35, 35);
  }
  &__content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    position: relative;
    @media (max-width: 768px) {
      display: none;
