      class="preview-video"
      autoplay="autoplay"
      :loop="true"
      playsinline
      src="@/assets/video/bg.mp4"
      muted="muted"
    ></video>
    <video
      class="preview-videoMob"
      autoplay="autoplay"
      :loop="true"
      playsinline
      src="@/assets/video/bgMob.mp4"
      muted="muted"
