          ></p>
          <button class="header__content-info-btn" @click="openModal">
            {{ btn[activeLanguage] }}
            <font-awesome-icon
              class="icon"
              icon="fa-solid fa-circle-arrow-right"
            />
          </button>
        </div>
        <!-- <div class="header__content-info-img">
          <img
            class="header__content-info-img-one"
            src="../assets/video/bg.mp4"
            alt=""
          />
          <img
            class="header__content-info-img-two"
            src="../assets/images/puzzles/2.png"
            alt=""
          />
        </div> -->
      </div>
    </div>
  </div>
  <PuzzleModal :show="show" @close="closeModal"></PuzzleModal>
</template>


<script>
import PuzzleModal from "./modal/PuzzleModal.vue";
export default {
  components: { PuzzleModal },
  data() {
    return {
      show: false,
      title: { ru: "PUZZLE", uz: `PUZZLE`, en: "PUZZLE" },
      descr: {
        ru: "<span>PUZZLE AGENCY</span> — эффективный и надежный партнер в поиске и формировании маркетинговой информации от потребителей до торговых объектов",
        uz: `<span>PUZZLE AGENCY</span> `,
        en: "<span>PUZZLE AGENCY</span> ",
      },
      btn: { ru: "Оставить заявку", uz: `So'rov qoldirish`, en: "" },
    };
  },

  methods: {
    goToTop() {
      window.scrollTo(0, 0);
    },
    openModal() {
      this.show = true;
    },
    closeModal() {
      this.show = false;
    },
  },
  computed: {
    activeLanguage() {
      return this.$store.getters.getLanguage;
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
.header {
  width: 100%;
  // max-width: 1920px;
  margin: 0 auto;
  background: linear-gradient(174.14deg, #3034ac 4.65%, #92147c 95.47%);
  position: relative;
  overflow: hidden;
  .preview-video {
    width: 100%;
    min-height: 100%;
    object-fit: cover;
    filter: brightness(100%);
    user-select: none;
    @media (max-width: 768px) {
      display: none;
    }
  }
  .preview-videoMob {
    display: none;
    @media (max-width: 768px) {
      display: block;
      width: 100%;
      @include adaptivHeightmd(1000, 600);
      object-fit: cover;
      filter: brightness(90%);
    }
  }
  &__content {
    width: 100%;
    display: flex;
    &-info {
      width: 40%;
      display: flex;
      flex-direction: column;
      @include adaptivPaddinglg(0, 0, 150, 180, 0, 0, 100, 100);
      position: absolute;
      top: 0;
      @media (max-width: 768px) {
        width: 80%;
        @include adaptivPaddingmd(0, 0, 250, 0, 0, 0, 150, 0);
        justify-content: center;
        align-items: center;
      }
      &-title {
        @include adaptiv-fontlg(110, 70);
        @include adaptiv-lineHeightlg(134, 90);
        color: #fff;
        font-family: "Montserrat-extraBold";
        @media (max-width: 768px) {
          @include adaptiv-fontmd(115, 55);
          @include adaptiv-lineHeightmd(130, 67);
          @include adaptivMarginmd(0, 0, 0, 20, 0, 0, 0, 10);
        }
      }
      &-descr {
        @include adaptiv-fontlg(30, 16);
        @include adaptiv-lineHeightlg(40, 20);
        color: #fff;
        font-family: "Montserrat-light";
        span {
          font-family: "Montserrat-bold";
        }
        @media (max-width: 768px) {
          @include adaptiv-fontmd(32, 14);
          @include adaptiv-lineHeightmd(39, 20);
          text-align: center;
        }
      }
      &-btn {
        @include adaptivWidthLg(282, 170);
        border: 3px solid #2f2633;
        box-shadow: -8px 8px 0px 0px #2f2633;
        background: #fff;
        color: #2f2633;
        @include adaptiv-fontlg(20, 13);
        @include adaptiv-lineHeightlg(20, 13);
        font-family: "Montserrat-medium";
        @include adaptivPaddinglg(0, 0, 28, 28, 0, 0, 14, 14);
        @include adaptivMarginlg(0, 0, 100, 0, 0, 0, 50, 0);
        transition: 0.5s;
        &:hover {
          box-shadow: -8px 8px 0px 0px #2fdef6;
          transition: box-shadow 0.5s;
        }
        .icon {
          @include adaptiv-fontlg(16, 9);
          @include adaptiv-lineHeightlg(20, 13);
          color: #535a61;
          @media (max-width: 768px) {
            @include adaptiv-fontmd(18, 10);
          }
        }
        @media (max-width: 768px) {
          @include adaptivWidthmd(300, 180);
          width: 100%;
          @include adaptiv-fontmd(22, 14);
          @include adaptiv-lineHeightmd(22, 14);
          @include adaptivPaddingmd(0, 0, 30, 30, 0, 0, 15, 15);
          @include adaptivMarginmd(0, 0, 70, 0, 0, 0, 50, 0);
        }
      }
      &-img {
        width: 100%;
        @media (max-width: 768px) {
          width: auto;
        }
        &-one {
          @include adaptivWidthLg(565, 280);
          position: absolute;
          bottom: 0;
          z-index: 1;
          width: 100%;
          transform: translate(-20%, 0%);
          animation: firstPuzzle 8s ease-in;
          @media (min-width: 1921px) {
            transform: translate(-21%, 0%);
