            bottom: -3%;
          }
          @media (max-width: 768px) {
            @include adaptivWidthmd(300, 150);
            animation: none;
            top: 9%;
            left: 27%;
            transform: translate(-14%, 12%);
          }
        }
        &-two {
          @include adaptivWidthLg(500, 250);
          width: 100%;
          position: absolute;
          right: -45px;
          top: 52px;
          transform: translate(-20%, 0%);
          animation: secondPuzzle 8s ease-in-out;
