@keyframes firstPuzzle {
  0% {
    transform: translate(-20%, 0%);
  }
  30% {
    transform: translate(-40%, 20%);
  }
  60% {
    transform: translate(-50%, 20%);
  }
  100% {
    transform: translate(-20%, 0%);
  }
}
@keyframes secondPuzzle {
  0% {
    transform: translate(-20%, 0%);
  }
  50% {
    transform: translate(20%, 0%);
  }
  100% {
    transform: translate(-20%, 0%);
  }
}
</style>